import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux'
import { createStore, combineReducers } from 'redux';
import gameStartReducer from './store/reducers/gameStartReducer';
import friendlyShipsReducer from './store/reducers/friendlyShipsReducer';
import lockedInReducer from './store/reducers/lockedInReducer';
import enemeShipsReducer from './store/reducers/enemyShipsReducer';
import turnReducer from './store/reducers/turnReducer';
import gameWonOrLostReducer from './store/reducers/gameWonOrLostReducer';

const rootReducer = combineReducers({
        gmstrtrdcr: gameStartReducer,
        frndlshpsrdcr: friendlyShipsReducer,
        lckdnrdcr: lockedInReducer,
        nmshpsrdcr: enemeShipsReducer,
        trnrdcr: turnReducer,
        gmwnrlstrdcr: gameWonOrLostReducer

})
const store = createStore(rootReducer)

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
    <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register();
