import * as actionTypes from '../actionTypes/actionTypes';

const initialState = {
    gameStart: false
}

const gameStartReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actionTypes.START_GAME:
            
        return {
            ...state,
            gameStart: true
        }    

        case actionTypes.GAME_RESTART :
                return {
                    ...state,
                    gameStart: false
                }
        default:
            break;
    }


    return state
}


export default gameStartReducer