import * as actionTypes from '../actionTypes/actionTypes';

const initialState = {
    didPlayerWin: false,
    didComputerWin: false
}

const gameWonOrLostReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actionTypes.PLAYER_WON:
            
        return {
            ...state,
            didPlayerWin: true
        }   
        
        case actionTypes.COMPUTER_WON:
            
        return {
            ...state,
            didComputerWin: true
        }   


        case actionTypes.GAME_RESTART :
                return {
                    ...state,
                    didPlayerWin: false,
                    didComputerWin: false
                }
        default:
            break;
    }


    return state
}


export default gameWonOrLostReducer