import React from 'react';
import { useDispatch } from 'react-redux';
import * as actionTypes from '../../../store/actionTypes/actionTypes';
import classes from './GameStart.module.css';
import ShipsOfBattleTitle from '../../../Images/ShipsOfBattleTitle.PNG'
import Sloop from '../../../Images/ShipOne.png'
import Caravel from '../../../Images/ShipTwo.png'
import Galleon from '../../../Images/ShipThree.png'
import Frigate from '../../../Images/ShipFour.png'


const GameStart = () => {

    const dispatch = useDispatch()

    const startGame = () => dispatch({type: actionTypes.START_GAME})

    return (
        <div className={classes.Container}>
            <img src={ShipsOfBattleTitle} alt="Title"></img>
            <button onClick={startGame}>Click to Play!</button>


            <img className={classes.Sloop} src={Sloop} alt="Sloop"></img>
            <img className={classes.Caravel} src={Caravel} alt="Caravel"></img>
            <img className={classes.Galleon} src={Galleon} alt="Galleon"></img>
            <img className={classes.Frigate} src={Frigate} alt="Frigate"></img>

        </div>
    )
}


export default GameStart
