import * as actionTypes from '../actionTypes/actionTypes';
import ShipOne from '../../Images/ShipOne.png';
import ShipTwo from '../../Images/ShipTwo.png';
import ShipThree from '../../Images/ShipThree.png';
import ShipFour from '../../Images/ShipFour.png';

const initialState = {
    friendlyShips: [
        {
            name: 'ShipOne',
            pathway: ShipOne,
            squares: 2,
            placement: []
        },
        {
            name: 'ShipTwo',
            pathway: ShipTwo,
            squares: 3,
            placement: []
        },
        {
            name: 'ShipThree',
            pathway: ShipThree,
            squares: 4,
            placement: []
        },
        {
            name: 'ShipFour',
            pathway: ShipFour,
            squares: 5,
            placement: []
        }
    ]
}

const friendlyShipsReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actionTypes.ADD_PLACEMENT: 
        let friendlyShipsCopy = [...state.friendlyShips]        
        
         
        friendlyShipsCopy[action.counter].placement = action.placementArray

        return {
            ...state,
            friendlyShips: friendlyShipsCopy
        }    

        case actionTypes.FRIENDLY_WAS_HIT:
            let friendlyShipsCopyTwo = [...state.friendlyShips] 


            friendlyShipsCopyTwo.forEach((element) => {
                if(element.placement.includes(action.square)) {
                    let index = element.placement.indexOf(action.square)
                    element.placement.splice(index, 1)
                }
            })

            return {
                ...state,
                friendlyShips: friendlyShipsCopyTwo
            }

        case actionTypes.GAME_RESTART :
                return {
                    ...state,
                    friendlyShips: [
                        {
                            name: 'ShipOne',
                            pathway: ShipOne,
                            squares: 2,
                            placement: []
                        },
                        {
                            name: 'ShipTwo',
                            pathway: ShipTwo,
                            squares: 3,
                            placement: []
                        },
                        {
                            name: 'ShipThree',
                            pathway: ShipThree,
                            squares: 4,
                            placement: []
                        },
                        {
                            name: 'ShipFour',
                            pathway: ShipFour,
                            squares: 5,
                            placement: []
                        }
                    ]
                }

        default:
            break;
    }


    return state
}


export default friendlyShipsReducer