import * as actionTypes from '../actionTypes/actionTypes';

const initialState = {
    isPlayerTurn: true
}

const turnReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actionTypes.PLAYER_TURN:
            
        return {
            ...state,
            isPlayerTurn: true
        }   
        
        case actionTypes.COMPUTER_TURN:
            
        return {
            ...state,
            isPlayerTurn: false
        }  
        
        

        case actionTypes.GAME_RESTART :
                return {
                    ...state,
                    isPlayerTurn: true
                }
        default:
            break;
    }


    return state
}


export default turnReducer