import classes from './Battle.module.css';
import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import * as actionTypes from '../../../store/actionTypes/actionTypes';
import EnemyShipSunk from './EnemyShipSunk/EnemyShipSunk';
import shipOneFirst from '../../../Images/ShipOneFirst.png';
import shipOneSecond from '../../../Images/ShipOneSecond.png';
import shipOneFirstY from '../../../Images/ShipOneFirstY.png';
import shipOneSecondY from '../../../Images/ShipOneSecondY.png';
import shipTwoFirst from '../../../Images/ShipTwoFirst.png';
import shipTwoSecond from '../../../Images/ShipTwoSecond.png';
import shipTwoThird from '../../../Images/ShipTwoThird.png';
import shipTwoFirstY from '../../../Images/ShipTwoFirstY.png';
import shipTwoSecondY from '../../../Images/ShipTwoSecondY.png';
import shipTwoThirdY from '../../../Images/ShipTwoThirdY.png';
import shipThreeFirst from '../../../Images/ShipThreeFirst.png';
import shipThreeSecond from '../../../Images/ShipThreeSecond.png';
import shipThreeThird from '../../../Images/ShipThreeThird.png';
import shipThreeFourth from '../../../Images/ShipThreeFourth.png';
import shipThreeFirstY from '../../../Images/ShipThreeFirstY.png';
import shipThreeSecondY from '../../../Images/ShipThreeSecondY.png';
import shipThreeThirdY from '../../../Images/ShipThreeThirdY.png';
import shipThreeFourthY from '../../../Images/ShipThreeFourthY.png';

import shipFourFirst from '../../../Images/ShipFourFirst.png';
import shipFourSecond from '../../../Images/ShipFourSecond.png';
import shipFourThird from '../../../Images/ShipFourThird.png';
import shipFourFourth from '../../../Images/ShipFourFourth.png';
import shipFourFifth from '../../../Images/ShipFourFifth.png';
import shipFourFirstY from '../../../Images/ShipFourFirstY.png';
import shipFourSecondY from '../../../Images/ShipFourSecondY.png';
import shipFourThirdY from '../../../Images/ShipFourThirdY.png';
import shipFourFourthY from '../../../Images/ShipFourFourthY.png';
import shipFourFifthY from '../../../Images/ShipFourFifthY.png';


import friendlyWatersImage from '../../../Images/Friendly Waters.png'
import enemyWatersImage from '../../../Images/UnchartedWaters.png'
import playerTurnImage from '../../../Images/AlliedPhase.png'
import computerTurnImage from '../../../Images/EnemyPhase.png'

const Battle = () => {

    const dispatch = useDispatch();


    let isPlayerTurn = useSelector(state => state.trnrdcr.isPlayerTurn)

    let friendlyShips = useSelector(state => state.frndlshpsrdcr.friendlyShips)
    let friendlyShipOnePlacement = friendlyShips[0].placement
    let friendlyShipTwoPlacement = friendlyShips[1].placement
    let friendlyShipThreePlacement = friendlyShips[2].placement
    let friendlyShipFourPlacement = friendlyShips[3].placement
    let friendlyGrid = []

    const [friendlyHitArray, setFriendlyHitArray] = useState([])
    const [friendlyMissedArray, setFriendlyMissedArray] = useState([])
    const [doNotAllow, setDoNotAllow] = useState([])
    const [friendlyFoundAt, setFriendlyFoundAt] = useState(false)
    const [friendlyJustSunk, setFriendlyJustSunk] = useState(false)
    const [friendlySunkArray, setFriendlySunkArray] = useState([])

    const [lockInShips, setLockInShips] = useState(true)

    const [friendlyShipOneLocked, setFriendlyShipOneLocked] = useState([])
    const [friendlyShipTwoLocked, setFriendlyShipTwoLocked] = useState([])
    const [friendlyShipThreeLocked, setFriendlyShipThreeLocked] = useState([])
    const [friendlyShipFourLocked, setFriendlyShipFourLocked] = useState([])


    if(lockInShips) {
        let oneLock = [...friendlyShipOnePlacement]
        let twoLock = [...friendlyShipTwoPlacement]
        let threeLock = [...friendlyShipThreePlacement]
        let fourLock = [...friendlyShipFourPlacement]
        setFriendlyShipOneLocked(oneLock)
        setFriendlyShipTwoLocked(twoLock)
        setFriendlyShipThreeLocked(threeLock)
        setFriendlyShipFourLocked(fourLock)
        setLockInShips(false)
        
    }

    let friendlyShipsArray = friendlyShipOnePlacement.concat(friendlyShipTwoPlacement).concat(friendlyShipThreePlacement).concat(friendlyShipFourPlacement)

    const computerAttacks = () => {
        let computerClicks = [];
        for(let i =0; i< 100; i++) {
            computerClicks.push(i);
        }

        doNotAllow.forEach(ele => {
            computerClicks.forEach(element => {
                if(ele === element) {
                    computerClicks.splice(computerClicks.indexOf(element), 1)
                }
            })

        })
   

        let square = computerClicks[Math.floor(Math.random()*computerClicks.length)]
        let computerClicksArray = friendlyMissedArray.concat(friendlyHitArray)
        if(friendlyShipOnePlacement.length < 2 && friendlyShipOnePlacement.length !== 0 && !friendlyFoundAt) {
            square = friendlyShipOnePlacement[0]

        }
        if(friendlyShipTwoPlacement.length < 3 && friendlyShipTwoPlacement.length !== 0 && !friendlyFoundAt) {
            square = friendlyShipTwoPlacement[0]

        }
        if(friendlyShipThreePlacement.length < 4 && friendlyShipThreePlacement.length !== 0 && !friendlyFoundAt) {
            square = friendlyShipThreePlacement[0]

        }
        if(friendlyShipFourPlacement.length < 5 && friendlyShipFourPlacement.length !== 0 && !friendlyFoundAt) {
            square = friendlyShipFourPlacement[0]

        }

        if(!square){
            square = computerClicks[Math.floor(Math.random()*computerClicks.length)]
            computerClicks.forEach((element, index) => {
                computerClicksArray.forEach((ele) => {
                    if(element === ele) {
                        computerClicks.splice(index, 1)
                    }
                })
            })
            computerClicks.forEach((element, index) => {
                doNotAllow.forEach((ele) => {
                    if(element === ele) {
                        computerClicks.splice(index, 1)
                    }
                })
            })

        }
        if(friendlyJustSunk){
            setFriendlyFoundAt(false)
            setFriendlyJustSunk(false)
        }
        
        if(friendlyFoundAt) {
            



            square = friendlyJustSunk ? square : friendlyFoundAt - 10

            

            if(computerClicksArray.includes(square) || square < 0) {
                square = friendlyFoundAt + 10
            }
             if(computerClicksArray.includes(square) || square > 99 ) {
                square = friendlyFoundAt + 1
            }
             if(computerClicksArray.includes(square) || square > 99) {
                square = friendlyFoundAt - 1
            }
            
            if(friendlySunkArray[friendlySunkArray.length -2]) {

                if (friendlySunkArray[friendlySunkArray.length - 1] - friendlySunkArray[friendlySunkArray.length - 2] === 1 ) {
                    square = friendlySunkArray[friendlySunkArray.length - 1] + 1
                }
                if (friendlySunkArray[friendlySunkArray.length - 1] - friendlySunkArray[friendlySunkArray.length - 2] === -1 ) {
                    square = friendlySunkArray[friendlySunkArray.length - 1] - 1
                }
                if (friendlySunkArray[friendlySunkArray.length - 1] - friendlySunkArray[friendlySunkArray.length - 2] === -10 ) {
                    square = friendlySunkArray[friendlySunkArray.length - 1] + 10
                }
                if (friendlySunkArray[friendlySunkArray.length - 1] - friendlySunkArray[friendlySunkArray.length - 2] === 10 ) {
                    square = friendlySunkArray[friendlySunkArray.length - 1] - 10
                }
            }
            

            
            if(computerClicksArray.includes(square)) {
                
                
                if(friendlyShipOnePlacement.length < 2 && friendlyShipFourPlacement.length !== 0) {
                    square = friendlyShipOnePlacement[0]
                }
                if(friendlyShipTwoPlacement.length < 3 && friendlyShipFourPlacement.length !== 0) {
                    square = friendlyShipTwoPlacement[0]
                }
                if(friendlyShipThreePlacement.length < 4 && friendlyShipFourPlacement.length !== 0) {
                    square = friendlyShipThreePlacement[0]
                }
                if(friendlyShipFourPlacement.length < 5 && friendlyShipFourPlacement.length !== 0) {
                    square = friendlyShipFourPlacement[0]
                }
                if(computerClicksArray.includes(square) || !square) {
                    computerClicks.forEach((element, index) => {
                        computerClicksArray.forEach((ele) => {
                            if(element === ele) {
                                computerClicks.splice(index, 1)
                            }
                        })
                    })
                    
                    
                    square = computerClicks[Math.floor(Math.random()*computerClicks.length)]
                    setFriendlyFoundAt(false)
                }
            }

            // //Delete after testing
            // square = friendlyShipsArray[0]

            

            


            if(friendlyShipsArray.includes(square)){
                if(friendlyShipOnePlacement.includes(square) && friendlyShipOnePlacement.length === 1) {
                    setFriendlyJustSunk(true)
                }
                if(friendlyShipTwoPlacement.includes(square) && friendlyShipTwoPlacement.length === 1) {
                    setFriendlyJustSunk(true)

                }
                if(friendlyShipThreePlacement.includes(square) && friendlyShipThreePlacement.length === 1) {
                    setFriendlyJustSunk(true)

                }
                if(friendlyShipFourPlacement.includes(square) && friendlyShipFourPlacement.length === 1 ) {
                    setFriendlyJustSunk(true)

                }

                // //Delete after testing
                // square = friendlyShipsArray[0]

                



                let friendlyHitArrayCopy = friendlyHitArray.concat([square])
                setFriendlyHitArray(friendlyHitArrayCopy)
                dispatch({type: actionTypes.FRIENDLY_WAS_HIT, square: square})
                setTimeout(() => {dispatch({type: actionTypes.PLAYER_TURN})}, 1000)
                setFriendlyFoundAt(square)

                
                
            } else {
                let friendlyMissedArrayCopy = friendlyMissedArray.concat([square])
                setFriendlyMissedArray(friendlyMissedArrayCopy)
                setTimeout(() => {dispatch({type: actionTypes.PLAYER_TURN})}, 1000)
            }

        } else {
            // //Delete after testing
            // square = friendlyShipsArray[0]
            
            if(friendlyShipsArray.includes(square)){
                let friendlyHitArrayCopy = friendlyHitArray.concat([square])
                setFriendlyHitArray(friendlyHitArrayCopy)
                dispatch({type: actionTypes.FRIENDLY_WAS_HIT, square: square})
                setTimeout(() => {dispatch({type: actionTypes.PLAYER_TURN})}, 1000)
                setFriendlyFoundAt(square)
            } else {
                let friendlyMissedArrayCopy = friendlyMissedArray.concat([square])
                setFriendlyMissedArray(friendlyMissedArrayCopy)
                setTimeout(() => {dispatch({type: actionTypes.PLAYER_TURN})}, 1000)
            }
        }

        if(friendlyShipsArray.includes(square)) {
            let friendlySunkArrayCopy = [...friendlySunkArray.concat([square])]
            setFriendlySunkArray(friendlySunkArrayCopy)
        }
        

        console.log('[Battle.js]   Square:   '  + square)
        let doNotAllowCopy = [...doNotAllow.concat([square])]
        setDoNotAllow(doNotAllowCopy)     

    }

    let shipOnePictureNumber = 0
    let shipTwoPictureNumber = 2
    let shipThreePictureNumber = 5
    let shipFourPictureNumber = 9

    const shipPicturesArray = [shipOneFirst, shipOneSecond, 
        shipTwoFirst, shipTwoSecond, shipTwoThird, 
        shipThreeFirst, shipThreeSecond, shipThreeThird, shipThreeFourth, 
        shipFourFirst, shipFourSecond, shipFourThird, shipFourFourth, shipFourFifth]
    const shipPicturesArrayY = [shipOneFirstY, shipOneSecondY, 
        shipTwoFirstY, shipTwoSecondY, shipTwoThirdY, 
        shipThreeFirstY, shipThreeSecondY, shipThreeThirdY, shipThreeFourthY, 
        shipFourFirstY, shipFourSecondY, shipFourThirdY, shipFourFourthY, shipFourFifthY]


    const createFriendlyGrid = () => {
        for(let i =0; i<100; i++){
            let square = <div key={i} className={classes.FriendlyEmptySquare}></div>
            if(friendlyShipOnePlacement.includes(i)) {
                if (friendlyShipOneLocked[1] - friendlyShipOneLocked[0] === 1  ) {
                    square = <div className={classes.FriendlyShipOne} style={{
                        backgroundImage: 'url(' + shipPicturesArray[shipOnePictureNumber] + ')'
                    }} key={i}></div>
                } else {
                    square = <div className={classes.FriendlyShipOne} style={{
                        backgroundImage: 'url(' + shipPicturesArrayY[shipOnePictureNumber] + ')'
                    }} key={i}></div>
                }
                shipOnePictureNumber = shipOnePictureNumber + 1
            }

            if(friendlyShipTwoPlacement.includes(i)) {
                if (friendlyShipTwoLocked[1] - friendlyShipTwoLocked[0] === 1  ) {
                    square = <div className={classes.FriendlyShipTwo} style={{
                        backgroundImage: 'url(' + shipPicturesArray[shipTwoPictureNumber] + ')'
                    }} key={i}></div>
                } else {
                    square = <div className={classes.FriendlyShipTwo} style={{
                        backgroundImage: 'url(' + shipPicturesArrayY[shipTwoPictureNumber] + ')'
                    }} key={i}></div>
                }
                shipTwoPictureNumber = shipTwoPictureNumber + 1
            }

            if(friendlyShipThreePlacement.includes(i)) {
                if (friendlyShipThreeLocked[1] - friendlyShipThreeLocked[0] === 1  ) {
                    square = <div className={classes.FriendlyShipThree} style={{
                        backgroundImage: 'url(' + shipPicturesArray[shipThreePictureNumber] + ')'
                    }} key={i}></div>
                } else {
                    square = <div className={classes.FriendlyShipThree} style={{
                        backgroundImage: 'url(' + shipPicturesArrayY[shipThreePictureNumber] + ')'
                    }} key={i}></div>
                }
                shipThreePictureNumber = shipThreePictureNumber + 1
            }

            if(friendlyShipFourPlacement.includes(i)) {
                if (friendlyShipFourLocked[1] - friendlyShipFourLocked[0] === 1  ) {
                    square = <div className={classes.FriendlyShipFour} style={{
                        backgroundImage: 'url(' + shipPicturesArray[shipFourPictureNumber] + ')'
                    }} key={i}></div>
                } else {
                    square = <div className={classes.FriendlyShipFour} style={{
                        backgroundImage: 'url(' + shipPicturesArrayY[shipFourPictureNumber] + ')'
                    }} key={i}></div>
                }
                shipFourPictureNumber = shipFourPictureNumber + 1
            }


            

            if(friendlyHitArray.includes(i) && friendlyShipOneLocked.includes(i)) {
                square = <div key={i} className={classes.FriendlyShipHit} ></div>
                shipOnePictureNumber = shipOnePictureNumber + 1
            }

            if(friendlyHitArray.includes(i) && friendlyShipTwoLocked.includes(i)) {
                square = <div key={i} className={classes.FriendlyShipHit} ></div>
                shipTwoPictureNumber = shipTwoPictureNumber + 1
            }

            if(friendlyHitArray.includes(i) && friendlyShipThreeLocked.includes(i)) {
                square = <div key={i} className={classes.FriendlyShipHit} ></div>
                shipThreePictureNumber = shipThreePictureNumber + 1
            }

            if(friendlyHitArray.includes(i) && friendlyShipFourLocked.includes(i)) {
                square = <div key={i} className={classes.FriendlyShipHit} ></div>
                shipFourPictureNumber = shipFourPictureNumber + 1
            }




            if(friendlyMissedArray.includes(i)) {
                square = <div key={i} className={classes.FriendlyShipMissed}></div>
            }
            friendlyGrid.push(square)
        }

    }

    createFriendlyGrid();








    let enemyGrid = []
    let enemyShipOnePlacement = useSelector(state => state.nmshpsrdcr.enemyShipOne)
    let enemyShipTwoPlacement = useSelector(state => state.nmshpsrdcr.enemyShipTwo)
    let enemyShipThreePlacement = useSelector(state => state.nmshpsrdcr.enemyShipThree)
    let enemyShipFourPlacement = useSelector(state => state.nmshpsrdcr.enemyShipFour)

    let enemyShipsArray = enemyShipTwoPlacement.concat(enemyShipOnePlacement).concat(enemyShipThreePlacement).concat(enemyShipFourPlacement)
    
    const [enemyShipOneAlert, setEnemyShipOneAlert] = useState(true)
    const [enemyShipTwoAlert, setEnemyShipTwoAlert] = useState(true)
    const [enemyShipThreeAlert, setEnemyShipThreeAlert] = useState(true)
    const [enemyShipFourAlert, setEnemyShipFourAlert] = useState(true)
    const [enemyShipSunkAlert, setEnemyShipSunkAlert] = useState(false)
   
        useEffect(() => {
            if (enemyShipOnePlacement.length === 0 && enemyShipOneAlert)  {
                setEnemyShipOneAlert(false)
                setEnemyShipSunkAlert(true)
            } 
            if (enemyShipTwoPlacement.length === 0 && enemyShipTwoAlert)  {
                setEnemyShipTwoAlert(false)
                setEnemyShipSunkAlert(true)}

            if (enemyShipThreePlacement.length === 0 && enemyShipThreeAlert)  {
                setEnemyShipThreeAlert(false)
                setEnemyShipSunkAlert(true)}

            if (enemyShipFourPlacement.length === 0 && enemyShipFourAlert)  {
                setEnemyShipFourAlert(false)
                setEnemyShipSunkAlert(true)}

        }, [enemyShipOnePlacement.length, enemyShipOneAlert, enemyShipTwoPlacement.length, enemyShipTwoAlert, enemyShipThreePlacement.length, enemyShipThreeAlert, enemyShipFourPlacement.length, enemyShipFourAlert])
    


    let enemyShipSunk = null;

    if(enemyShipSunkAlert) {
        enemyShipSunk = <EnemyShipSunk />
        setTimeout(()=>{
            setEnemyShipSunkAlert(false)
        }, 2000)
    }

    const [enemyHitArray, setEnemyHitArray] = useState([])
    const [enemyMissedArray, setEnemyMissedArray] = useState([])
    

    const playerHitAnEnemy = (square) => {
        
        if(isPlayerTurn) {
            if(enemyShipsArray.includes(square)){
                let enemyHitArrayCopy = enemyHitArray.concat([square])
                setEnemyHitArray(enemyHitArrayCopy)
                dispatch({type: actionTypes.ENEMY_WAS_HIT, square: square})
                setTimeout(()=>{
                    computerAttacks();
                },1500)
                dispatch({type: actionTypes.COMPUTER_TURN})
            } else {
                let enemyMissedArrayCopy = enemyMissedArray.concat([square])
                setEnemyMissedArray(enemyMissedArrayCopy)
                setTimeout(()=>{
                    computerAttacks();
                },1500)
                dispatch({type: actionTypes.COMPUTER_TURN})
            }
        }
    }

    const createEnemyGrid = () => {
        for(let i =0; i<100; i++){
            let square = <div key={i} className={classes.EnemyEmptySquare} onClick={() => {playerHitAnEnemy(i)}}></div>
            

            if(enemyHitArray.includes(i)) {
                square = <div key={i} className={classes.EnemyShipHit} ></div>
            }

            if(enemyMissedArray.includes(i)) {
                square = <div key={i} className={classes.EnemyShipMissed} ></div>
            }
            enemyGrid.push(square)
        }

    }

    createEnemyGrid();

    

    let enemyWaters = <div className={classes.EnemyWaters}>
                            <div className={classes.WhoseTurnEnemy}>{<img src={enemyWatersImage} alt="turnImg" />}   </div>
                            <div className={classes.EnemyDecorations}>
                                <div className={classes.EnemyGrid}>
                                    {enemyGrid.map(e => e)}
                                </div>
                            </div>

                        </div>


    let friendlyWaters = <div className={classes.FriendlyWaters}>
                            <div className={classes.WhoseTurn}>{<img src={friendlyWatersImage} alt="turnImg" />}   </div>
                            <div className={classes.FriendlyGrid}>
                                {friendlyGrid.map(e => e)}
                            </div>
                        </div>
    if(friendlyShipsArray.length === 0) {
        dispatch({type: actionTypes.COMPUTER_WON})
    }
    if(enemyShipsArray.length === 0) {
        dispatch({type: actionTypes.PLAYER_WON})
    }

    return (
        <div className={classes.BattleContainer}>
            <div className={classes.BattleSplitter}>

                {enemyWaters}
                <div className={classes.TurnTitle}>
                    {isPlayerTurn ? <img src={playerTurnImage} alt="turnImg" /> : <img src={computerTurnImage} alt="turnImg" />}
                </div>
                {friendlyWaters}
            </div>
        {enemyShipSunk}
        </div>
    )
}

export default Battle