import React from 'react';
import classes from './GameWon.module.css';
import alufy from '../../../Images/Victory.png'
import { useDispatch } from 'react-redux';
import * as actionTypes from '../../../store/actionTypes/actionTypes'; 

const GameWon = () => {
    const dispatch = useDispatch();
    const playAgainHandler = () => {
        dispatch({type: actionTypes.GAME_RESTART})
    }
    return (
        <div className={classes.Container}>
            <div className={classes.VictoryDiv}>
            <img src={alufy} alt="won" />

            </div>
            <div>
            <button className={classes.PlayAgain} onClick={playAgainHandler}>Play again?</button>

            </div>
                <p className={classes.Title}>Well done. <br />
                 You played a part in ending the lives of countless young men who were forced to fight and only longed for the day they could go home. 
                Each individual had a unique background along with hopes and dreams for the futue. Gone in an instant. Next time you press that button.  Remember. <br />It's not just the weight of your finger. </p>
                
        </div>
    )
}

export default GameWon