import React from 'react';
import { useSelector } from 'react-redux';
import SetUpStage from './Components/SetUpStage/SetUpStage';
import GameStart from './Components/GameStart/GameStart';
import Battle from './Components/Battle/Battle';
import GameWon from './Components/GameWon/GameWon';
import GameLost from './Components/GameLost/GameLost';


const ShipsOfBattle = () => {

    const didPlayerWin = useSelector(state => state.gmwnrlstrdcr.didPlayerWin)
    const didComputerWin = useSelector(state => state.gmwnrlstrdcr.didComputerWin)
    const isLockedIn = useSelector(state => state.lckdnrdcr.lockedIn)
    const isGameStart = useSelector(state => state.gmstrtrdcr.gameStart)
    let onScreen = isGameStart ? <SetUpStage /> : <GameStart />
    if(isLockedIn) {
        onScreen = <Battle />
    }
    if(didPlayerWin) {
        onScreen = <GameWon />
    }
    if(didComputerWin) {
        onScreen = <GameLost />
    }
    return (
        <div>
            {onScreen}
        </div>
    )
}

export default ShipsOfBattle