import classes from './EnemyShipSunk.module.css';
import React from 'react';
import Druid from '../../../../Images/DruidWin.png'


const EnemyShipSunk = () => {
    return (
        <div className={classes.Container}>
            <div className={classes.Modal}>
                <div className={classes.Druid}>
                    <img src={Druid} alt="Druid" ></img>
                </div>
                <p>ENEMY SHIP SUNK!</p>
            </div>
        </div>
    )
}


export default EnemyShipSunk