import React from 'react';
import classes from './GameLost.module.css';
import { useDispatch } from 'react-redux';
import * as actionTypes from '../../../store/actionTypes/actionTypes'; 

const GameLost = () => {
    const dispatch = useDispatch();
    const playAgainHandler = () => {
        dispatch({type: actionTypes.GAME_RESTART})
    }
    return (
        <div className={classes.Container}>
                <p className={classes.Title}>All your ships have sunk. Nothing but a memory now.</p>
                
                <button className={classes.PlayAgain} onClick={playAgainHandler}>Play again?</button>
        </div>
    )
}

export default GameLost