import * as actionTypes from '../actionTypes/actionTypes';

const initialState = {
    lockedIn: false
}

const lockInReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actionTypes.LOCKED_IN:
            
        return {
            ...state,
            lockedIn: true
        }    

        case actionTypes.GAME_RESTART :
                return {
                    ...state,
                    lockedIn: false
                }
        default:
            break;
    }


    return state
}


export default lockInReducer